import React, { useState, useEffect } from 'react';
import './App.css'; // or the appropriate path to your CSS file;
import logoImage from "./logo1.png";
import { Helmet } from 'react-helmet';

import companies from "./companies (17).json";
import newLogoImage from "./logo2.png";
import emoticonImage from "./fbemoticon.png"
import { auth } from './firebase';
import { onAuthStateChanged } from 'firebase/auth';
import SignIn from './SignIn'; // Import SignIn component

import ReactGA from 'react-ga';
ReactGA.initialize('G-VC136BSRFF');
ReactGA.pageview(window.location.pathname + window.location.search);


function SearchBox({ onSearchClick, searchTerm, onSearchTermChange, onExportCSV, onSectorChange }) {
    const handleKeyDown = (event) => {
        if (event.key === 'Enter') {
            onSearchClick();
        }
    };

    return (
        <div className="search-box-container">
            <div className="search-field-container">
                <input
                    type="text"
                    value={searchTerm}
                    onChange={onSearchTermChange}
                    onKeyDown={handleKeyDown} 
                    placeholder="Enter a company..."
                />
                <button onClick={onSearchClick}>Search</button>
            </div>


            <div className="sector-dropdown-container">
                <select onChange={onSectorChange}>
                    <option value="">Select a sector...</option>
                    {sectorOptions.map(option => (
                        <option key={option.value} value={option.value}>{option.label}</option>
                    ))}
                </select>
            </div>
        </div>
    );
} function CompanyDetails({ company, onBack }) {
    if (!company) return null;

    return (
        <div>
            <back-button onClick={onBack}>Back</back-button>
            <h2>{company.name}</h2>
            <p>Website: <a href={company.website} target="_blank" rel="noopener noreferrer">{company.website}</a></p>
            <h3>Employees</h3>
            <table>
                <thead>
                    <tr>
                        <th>Employee Name</th>
                        <th>Employee Title</th>
                        <th>Employee Email</th>
                        <th>LinkedIn</th>
                    </tr>
                </thead>
                <tbody>
                    {company.employees.map((employee, index) => (
                        <tr key={index}>
                            <td>{employee.name}</td>
                            <td>{employee.title}</td>
                            <td>{employee.email}</td>
                            <td>
                                <a href={employee.linkedIn} target="_blank" rel="noopener noreferrer">LinkedIn</a>
                            </td>
                        </tr>
                    ))}
                </tbody>
            </table>
            <back-button onClick={onBack}>Back</back-button>
        </div>
    );
}


function CompanyList({ companies, onSelect }) {
    const itemsPerPage = 100;
    const [currentPage, setCurrentPage] = useState(1);

    const pageCount = Math.ceil(companies.length / itemsPerPage);
    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentCompanies = companies.slice(indexOfFirstItem, indexOfLastItem);

    const paginate = (pageNumber) => setCurrentPage(pageNumber);

    return (
        <>
            <table>
                <thead>
                    <tr>
                        <th>Company Name</th>
                        <th>Industry Tags</th>
                        <th>Website</th>
                        <th>Employees</th>
                    </tr>
                </thead>
                <tbody>
                    {currentCompanies.map((company, index) => (
                        <tr key={index}>
                            <td>{company.name}</td>
                            <td>{company.tag}</td>
                            <td><a href={company.website} target="_blank" rel="noopener noreferrer">{company.website}</a></td>
                            <td className="center-align">
                                <button onClick={() => {
                                    onSelect(company);
                                    // Track button click
                                    ReactGA.event({
                                        category: 'Button',
                                        action: 'Click',
                                        label: 'Contacts Button'
                                    });
                                }}>Contacts</button>
                            </td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </>
    );
}

function CustomDatasetsTab() {
    return (
        <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center', margin: '0 30px' }}>
            <h2 style={{ color: '#4CAF50' }}>Custom Data</h2>
            <p style={{ textAlign: 'center' }}>Welcome to our platform, driven by a group of <strong>University of Virginia</strong> students and alumni with a deep passion for <strong>web scraping</strong> and <strong>data analysis</strong>.</p>
            <p style={{ textAlign: 'center' }}>Whether you're a burgeoning startup or an established corporation, our data can offer you a competitive edge. Interested in learning more? Get in touch at <strong><a href="mailto:wac6er@virginia.edu">wac6er@virginia.edu</a></strong> for tailored data solutions that suit your specific business needs.</p>
            <br />
            <p><b>1K-15K Commercial Contact Profiles by Industry, Geography, Size, (++): $100 per 1000 contacts</b></p>
        </div>
    );
}

const sectorOptions = [
    { value: 'Construction', label: 'Construction' },
    { value: 'Energy', label: 'Energy'},
    { value: 'Technology', label: 'Technology' },
    { value: 'Healthcare', label: 'Healthcare' },
    { value: 'Retail', label: 'Retail' },
    { value: 'Finance', label: 'Finance' },
    { value: 'Manufacturing', label: 'Manufacturing' },
    { value: 'Government and Public Services', label: 'Government and Public Services' },
    { value: 'Real Estate and Home Services', label: 'Real Estate and Home Services' },
    { value: 'Education and Training', label: 'Education and Training' }
];

function App() {
    const [user, setUser] = useState(null);
    const [searchTerm, setSearchTerm] = useState('solar'); 
    const [filteredCompanies, setFilteredCompanies] = useState([]);
    const [selectedCompany, setSelectedCompany] = useState(null);
    const [searchCount, setSearchCount] = useState(0);
    const [currentTab, setCurrentTab] = useState('companies');
    const [isMobile, setIsMobile] = useState(window.innerWidth < 768);
    const [sectorChanged, setSectorChanged] = useState(false);
    const [selectedSector, setSelectedSector] = useState('');

    useEffect(() => {
        const handleResize = () => {
            setIsMobile(window.innerWidth < 768);
        };

        window.addEventListener('resize', handleResize);
        return () => window.removeEventListener('resize', handleResize);
    }, []);


    useEffect(() => {
        if (searchTerm) {
            handleSearch();
        }
    }, []);

    useEffect(() => {
        if (sectorChanged) {
            handleSearch();
            setSectorChanged(false);
        }
    }, [sectorChanged]);


    const categoryTagsMapping = {
        'Construction': ['construction', 'contractor', 'roofing'],
        'Energy': ['utility','energy','solar_energy'],
        'Technology': ['technology', 'software', 'biotechnology'],
        'Healthcare': ['healthcare', 'pharmaceutical'],
        'Retail': ['retail', 'supermarket', 'grocery'],
        'Finance': ['financial_services', 'investment', 'banking'],
        'Manufacturing': ['manufacturing', 'industrial', 'engineering'],
        'Government and Public Services': ['government', 'utilities'],
        'Real Estate and Home Services': ['real_estate', 'home_improvement', 'home_services'],
        'Education and Training': ['education', 'consulting'] // 'Training' tag not available in top 50
    };
    const handleSectorChange = (event) => {
        const selectedCategory = event.target.value;
        setSelectedSector(selectedCategory); // Set the selected sector

        const applicableTags = categoryTagsMapping[selectedCategory] || [];
        const filtered = companies.filter(company =>
            company.tag.split(', ').some(tag => applicableTags.includes(tag.trim()))
        );

        setFilteredCompanies(filtered);
        setSelectedCompany(null);
        setSearchTerm('');
        setSectorChanged(true);
    };
    const handleExportCSV = () => {
        const csvRows = [];
        csvRows.push('Company Name, Industry Tags, Website, Name, Title, Email');

        const formatCell = (cell) => `"${cell.replace(/"/g, '""')}"`; 

        if (selectedCompany) {
           
            selectedCompany.employees.forEach(employee => {
                const row = [
                    formatCell(selectedCompany.name),
                    formatCell(selectedCompany.tag),
                    formatCell(selectedCompany.website),
                    formatCell(employee.name),
                    formatCell(employee.title),
                    formatCell(employee.email)
                ];
                csvRows.push(row.join(','));
            });
        } else {

            filteredCompanies.slice(0, 50).forEach(company => {
                company.employees.forEach(employee => {
                    const row = [
                        formatCell(company.name),
                        formatCell(company.tag),
                        formatCell(company.website),
                        formatCell(employee.name),
                        formatCell(employee.title),
                        formatCell(employee.email)
                    ];
                    csvRows.push(row.join(','));
                });
            });
        }

        const csvString = csvRows.join('\n');
        const blob = new Blob([csvString], { type: 'text/csv' });
        const url = window.URL.createObjectURL(blob);
        const a = document.createElement('a');
        a.setAttribute('hidden', '');
        a.setAttribute('href', url);
        a.setAttribute('download', 'export.csv');
        document.body.appendChild(a);
        a.click();
        document.body.removeChild(a);
    };
    

    const handleSearch = () => {
        let filtered;
        ReactGA.event({
            category: 'User',
            action: 'Search',
            label: searchTerm
        });
        if (selectedSector) {
            const applicableTags = categoryTagsMapping[selectedSector];
            filtered = companies.filter(company =>
                company.tag.split(', ').some(tag => applicableTags && applicableTags.includes(tag.trim()))
            );
        } else {
            filtered = companies.filter(company =>
                company.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
                (company.tag && company.tag.toLowerCase().includes(searchTerm.toLowerCase()))
            );
        }

        // Sort filtered companies alphabetically by name
        filtered.sort((a, b) => a.name.localeCompare(b.name));

        setFilteredCompanies(filtered);
        setSelectedCompany(null);
        setSearchCount(prevCount => prevCount + 1);
    };


    const handleSelectCompany = (company) => {
        setSelectedCompany(company);
    };


    return (
        <div className="content">
            <Helmet>
                <title>The largest open source B2B contact portal online</title>
                <meta name="description" content="Free access to marketing, sales, licensing, business development, entrepreneurship and more." />
            </Helmet>

            <div className="header sticky-header">

                <div className="tab-buttons">
                    <button onClick={() => setCurrentTab('companies')}>Public Company Search</button>
                    <button onClick={() => setCurrentTab('customDatasets')}>Custom Datasets</button>
                </div>
                <div className="title-and-logo">
                    <div className="spacer"></div>
                    <img src={newLogoImage} alt="New Logo" className="left-logo" />
                    <div className="title-container">
                        <h1>Inconsistent Contacts</h1>
                        <p className="subtitle">Welcome to the largest open source B2B contact portal that exists.</p>
                        <p className="subtitle">Search specific! Max of 100 companies displayed per search.</p>
                    </div>
                    <img src={logoImage} alt="Logo" className="right-logo" />
                    <div className="spacer"></div>
                </div>

            </div>

            {currentTab === 'companies' && (
                <>
                    {searchCount < 10 || user ? (
                        <>
                            <SearchBox
                                onSearchClick={handleSearch}
                                searchTerm={searchTerm}
                                onSearchTermChange={(e) => setSearchTerm(e.target.value)}
                                onExportCSV={handleExportCSV}
                                onSectorChange={handleSectorChange} // Pass the function as a prop
                            />
                            {filteredCompanies.length === 0 && !selectedCompany && (
                                <p style={{ marginTop: '20px', fontSize: '0.9rem' }}>
                                    <b>We don't currently have a company in our sytem that matches your key-word</b>.
                                    <br />
                                    <br />
                                    Could the company have a different formal name (ex: Google is actually Alphabet?)... Our goal is to break down the <b>barriers surrounding B2B communications</b> by offering a <b>free, reliable source</b> for lead generation.
                                    <br />
                                    <br />
                                    By leveraging the power of web scraping, we aim to make <b>email and LinkedIn communication</b> more accessible and efficient, especially for small businesses and entrepreneurs.
                                    <br />
                                    <br />
                                    <b>Your involvement is vital to our mission.</b>
                                    <br />
                                    <br />
                                    If you wish to <b>contribute</b> to the site's data management or have suggestions, please reach out to us at <b><a href="mailto:wac6er@virginia.edu">wac6er@virginia.edu</a></b>. To support our project further, consider visiting our <b>GoFundMe</b> page at <a href="https://gofund.me/5286faf4" target="_blank">gofundme/inconsistentContacts</a>. Thank you for joining us in this venture!
                                </p>
                            )}
                            {!selectedCompany ? (
                                <CompanyList companies={filteredCompanies} onSelect={handleSelectCompany} />
                            ) : (
                                <CompanyDetails company={selectedCompany} onBack={() => setSelectedCompany(null)} />
                            )}
                        </>
                    ) : (
                        <SignIn />
                    )}
                </>
            )}

            {currentTab === 'customDatasets' && <CustomDatasetsTab />}
        </div>
    );
}

export default App;
